body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
  min-height: 100vh;
  display:flex;
  flex-direction: column;
}

.HeadImg{
  position: absolute;
  height:500px;
  background: blue url(/static/media/Head.ac75bc35.png) no-repeat fixed center 100%;
  background-size: 100vmin ;

}
.panel {
  height: 100vh;
  width: 100vw;
  text-align: center;
}

.panel span {
  position:relative;
  display: block;
  overflow: visible;
  top: 30%;
  
}

.panel.blue {
  background-color: transparent;
  font-size: 80px;
}

.panel.turqoise {
  background-color: transparent;
  font-size: 50px;
  line-height: 1.5em;

}

.panel.green {
  background-color: transparent;

}

.panel.bordeaux {
  background-color: transparent;

}

